@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  font-family: 'Inter', sans-serif;
}

.main {
  background-color: rgb(36,36,36);
  width: 100%;
  height: 100vh;
  padding-top: 50px;
}
#root {
  background-color: rgb(36,36,36);
}
.spinner {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header{
    display: flex;
    align-items: center;
    width: 80%;
    background-color: rgb(36,36,36);
    min-width: 288px;
    margin-right:  auto;
    margin-left: auto;
    padding-top: 28px;
}
.header__links {
    display: flex;
    padding: 0;
}
.header__li {
    list-style-type: none;
    padding: 0;
    margin: 0 30px 0 0;
}
.header__link {
    list-style-type: none;
    padding: 0;
    color: white;
    font-size: 30px;
    border-bottom: 3px solid rgb(70,70,70);
    text-decoration: none;
}
.header__link-active {
    border-bottom: 3px solid white;
}
.header__link:last-of-type {
    margin-right: 0;
}
.calculator {
    width: 80%;
    margin: 50px auto 0;
    display: flex;
    flex-direction: column;
}
.calculator__interface {
    display: flex;
    justify-content: space-between;
}
.calculator__zone {
    background-color: rgb(70,70,70);
    border-radius: 8px;
    width: 350px;
    max-width: 500px;
    padding: 30px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.calculator__title {
    color: white;
    width: 100%;
    text-align: center;
    margin: 0 0 30px 0;
}
.calculator__fieldset {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.calculator__input {
    border: 0;
    outline: 0;
    background-color: transparent;
    border-bottom: 3px solid white;
    width: 100px;
    color: white;
}
.calculator__add {
    background-color: transparent;
    color: white;
    border: none;
    outline: none;
    font-size: 20px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: auto;
    opacity: 1;
}
.calculator__add:disabled {
    text-decoration: line-through;
    opacity: .8;
}
.calculator__add:hover {
    cursor: pointer;
}


.success {
    display: none;
    width: 100%;
    align-items: center;
    margin-top: 30px;
}
.success__shown {
    display: flex;
}
.success__image {
    width: 16px;
    height: 16px;
    object-fit: cover;
    margin-right: 15px;
}
.success__message {
    color: white;
}
.error {
    display: none;
    width: 100%;
    align-items: center;
    margin-top: 30px;
}
.error__shown {
    display: flex;
}
.error__image {
    width: 16px;
    height: 16px;
    object-fit: cover;
    margin-right: 15px;
}
.error__message {
    color: white;
}
.canvas {
    display: flex;
    position: relative;
    justify-content: flex-end;
}
.canvas-none {
    display: none;
}
.calculator__canvas {
    /* position: absolute; */
    width: 400px;
    height: 400px;  
    transform: rotate(90deg);
    margin-bottom: -80px;
}
.canvas__value {
    color: white;
    position: absolute;
}
.k1 {
    bottom: -20px;
    right: 50%;
}
.k2 {
    top: 50%;
    transform: translate(0, 50%);
}
.gyp {
    left: 0;
    top: 50%;
    transform: translate(0, 50%);
}
.calculator__answer {
    width: 100%;
    background-color: rgb(70,70,70);
    margin-top: 100px;
    border-radius: 8px;
    overflow: hidden;
}
.calculator__answer-hidden {
    display: none;
}
.card__text-404 {
    color: white;
    font-size: 30px;
    box-sizing: border-box;
    padding: 0 10px 10px;
}
.card {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 10px 5px 10px;
    background-color: rgb(25,25,25);
    box-sizing: border-box;
}
.card:first-of-type {
    background-color: rgb(10,10,10);
}
.card:nth-of-type(2n) {
    background-color: rgb(70,70,70);
}
.card__header{
    display: grid; 
    width: 100%;
    grid-template-columns: repeat(7, 1fr);
}
.card__num {
    color: white;
    font-size: 30px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    align-items: flex-end;
}
.card__view {
    background-color: transparent;
    outline: none;
    border: none;
    color: white;
    font-size: 30px;
    padding: 0;
    z-index: 5;
}
.card__view:hover {
    cursor: pointer;
}
.card__main {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
}
.card__main-hidden {
    display: none;
}
.card__data {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
